.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__inner {
    height: 100%;
    width: 20px;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #c3b4f3;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .chart-bar__fill {
      background-color: #4826b9;
      width: 100%;
      transition: all 0.3s ease-out;
    }

    .chart-bar__label {
      font-weight: bold;
      font-size: 0.5rem;
      text-align: center;
    }
  }
}

.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
}
